import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "./style.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post("http://localhost:3001/login", { email, password });

      if (response.status === 200) {
        sessionStorage.setItem("usuario", JSON.stringify(response.data.usuario)); // Guarda el usuario en sesin
        navigate("/dashboard"); // Redirige al usuario
      }
    } catch (error) {
      alert(" Correo o contrasea incorrectos.");
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2> Iniciar Sesin</h2>
        <form onSubmit={handleLogin}>
          <input type="email" placeholder="Correo electrnico" value={email} onChange={(e) => setEmail(e.target.value)} required />
          <input type="password" placeholder="Contrasea" value={password} onChange={(e) => setPassword(e.target.value)} required />
          <button type="submit">Ingresar</button>
        </form>
      </div>
    </div>
  );
};

export default Login;
